<template>
	<div class="wenjuan-main" :style="{minHeight: wrapHeight + 'px'}">
		<div v-if="nodata" class="no-data"></div>
		<template v-else>
			<div v-show="!loading" class="wenjuan-title">希望您本次培训给出建议和意见！</div>
			<ul v-show="!loading" class="wenjuan-list">
				<template v-for="(item, index) in data">
					<li :key="index" :id="'question'+index">
						<div class="question-type">{{types[item.subjectType]}}</div>
						<div class="question-content">
							<div class="question-title" :class="item.requiredFields?'require':''">{{index+1}}. {{item.title}}</div>
							<van-radio-group class="question-options" v-model="item.answer" v-if="item.subjectType===0">
								<template v-for="(option, i) in item.options">
									<van-radio :name="i+1">{{option.value}}</van-radio>
									<div class="custom-input" :class="option.isexpand&&item.answer===i+1?'isrequire':''" v-if="option.isexpand">
										<input type="text" v-model="option.expandval" :placeholder="option.isexpand&&item.answer===i+1?'必填':''" @focus="onRadioFocus(item, i)" />
									</div>
								</template>
							</van-radio-group>
							<van-checkbox-group class="question-options" v-model="item.answer" v-if="item.subjectType===1">
								<template v-for="(option, i) in item.options">
									<van-checkbox shape="square" :name="i+1">{{option.value}}</van-checkbox>
									<div class="custom-input" :class="option.isexpand&&item.answer.indexOf(i+1)>-1?'isrequire':''" v-if="option.isexpand">
										<input type="text" v-model="option.expandval" :placeholder="option.isexpand&&item.answer.indexOf(i+1)>-1?'必填':''" @focus="onCheckboxFocus(item, i)" />
									</div>
								</template>
							</van-checkbox-group>
							<div class="array-options" v-if="item.subjectType===2">
								<template v-for="(option, i) in item.options">
									<div class="arry-item">{{option.title}}</div>
									<van-radio-group v-model="item.answer[i]" direction="horizontal">
										<template v-for="(opt, j) in option.options">
											<van-radio :name="j+1">{{opt.value}}</van-radio>
										</template>
									</van-radio-group>
									<template v-for="(opt, j) in option.options">
										<div class="custom-input" :class="opt.isexpand&&item.answer[i]===j+1?'isrequire':''" v-show="opt.isexpand&&item.answer[i]===j+1">
											<input type="text" v-model="opt.expandval" :placeholder="opt.isexpand&&item.answer[i]===j+1?'必填':''" />
										</div>
									</template>
								</template>
							</div>
							<div class="text-area" v-if="item.subjectType===3">
								<textarea v-model="item.answer" rows="4" :maxlength="item.wordRestriction"></textarea>
								<p class="num-limit">{{item.answer.length}}/{{item.wordRestriction}}</p>
							</div>
						</div>
					</li>
				</template>
			</ul>
		</template>
		<div class="wenjuan-footer">
			<van-button round block type="primary" @click="onSubmit">提交</van-button>
		</div>
	</div>
</template>

<script>
	import { mapState } from "vuex"
	const types = ['单选题', '多选题', '矩阵题', '简答题']
	export default {
		name: 'question',
		data() {
			return {
				contentH: 0,
				gobackTimer: null,
				types: types,
				rate: 5,
				loading: true,
				nodata: false,
				data: {}
			}
		},
		computed: {
			...mapState(['info']),
			wrapHeight() {
				return this.contentH
			}
		},
		mounted() {
			this.$nextTick(() => {
					this.contentH = document.documentElement.clientHeight
				}),
				this.getData()
		},
		methods: {
			async getData() {
				let params = {
					studentId: this.info.id,
					estimateId: this.$route.query.queid
				}
				let res = await this.$Request.get('/estimate/getQuestionnaireByWeChat.do', params)
				this.loading = false
				if(res && res.code === 0) {
					let temp = res.list
					try {
						temp = temp && temp.content ? JSON.parse(temp.content) : []
					} catch(e) {
						//TODO handle the exception
						temp = []
					}
					//temp = temp.filter(item => item.subjectType === 3)
					//temp = [temp[0]]
					temp = this.formatData(temp)
					this.nodata = !temp.length
					this.data = temp
					//console.log(this.data)
				} else {
					this.nodata = true
					//this.$notify.clear()
				}
			},
			//格式化
			formatData(data) {
				let temp = JSON.parse(JSON.stringify(data))
				temp.forEach(item => {
					item.subjectType = parseInt(item.subjectType)
					item.requiredFields = parseInt(item.requiredFields)
					let qtype = item.subjectType
					item.options = []
					if(qtype === 0) { // 单选
						item.answer = null
						item.op.forEach((opt, i) => {
							item.options.push({
								index: i + 1,
								value: opt,
								score: item.score[i],
								isexpand: item.mustAnswer[i] * 1, //是否扩展回答
								expandval: '' //扩展回答
							})
						})
					}
					if(qtype === 1) { // 多选
						item.answer = []
						item.op.forEach((opt, i) => {
							item.options.push({
								index: i + 1,
								value: opt,
								score: item.score[i],
								isexpand: item.mustAnswer[i] * 1, //是否扩展回答
								expandval: '' //扩展回答
							})
						})
					}
					if(qtype === 2) { // 矩阵
						let len = item.opt.length
						item.answer = []
						item.opt.forEach((opts, j) => {
							let arr = []
							item.answer.push('')
							item.op.forEach((opt, i) => {
								arr.push({
									index: i + 1,
									value: opt,
									isexpand: item.mustAnswer[i] * 1, //是否扩展回答
									expandval: '' //扩展回答
								})
							})
							item.options.push({
								index: j + 1,
								title: opts,
								score: item.score[j],
								options: arr
							})
						})
					}
					if(qtype === 3) { // 简答
						item.answer = ''
					}
				})
				return temp
			},
			// 单选扩展输入聚焦选中
			onRadioFocus(item, i) {
				item.answer = i + 1
			},
			// 多选扩展输入聚焦选中
			onCheckboxFocus(item, i) {
				let index = item.answer.indexOf(i + 1)
				if(index === -1) {
					item.answer.push(i + 1)
				}
			},
			// 点击提交
			onSubmit() {
				let params = this.getParams()
				let notcheck = this.verify(params)
				if(notcheck) {
					this.$toast(notcheck)
				} else {
					this.$dialog.confirm({
						title: '是否提交问卷？',
						className: 'custom-confirm-dialog'
					}).then(() => {
						// on confirm
						this.sureSubmit(params)
					}).catch(() => {
						// on cancel
					})
				}
			},
			//获取参数
			getParams() {
				let temp = JSON.parse(JSON.stringify(this.data))
				temp.forEach((item, i) => {
					let filters = []
					let qtype = item.subjectType
					if(qtype === 0) { // 单选
						filters = item.options.filter(opt => opt.index === item.answer)
					}
					if(qtype === 1) { // 多选
						filters = item.options.filter(opt => item.answer.indexOf(opt.index) > -1)
					}
					if(qtype === 2) { // 矩阵
						item.options.forEach((opts, j) => {
							opts.options.forEach((opt, k) => {
								//console.log(item.answer, opt.index)
								let active = item.answer[j] && item.answer[j] === opt.index
								if(active) {
									filters.push({
										optindex: opts.index,
										index: opt.index,
										value: item.answer[j],
										score: item.score[j],
										isexpand: opt.isexpand,
										expandval: opt.expandval
									})
								}
							})
						})
					}
					if(qtype === 3) { // 简答
						if(item.answer.length) {
							filters = [{
								value: item.answer,
								score: item.score || '',
								expandval: item.answer
							}]
						}
					}
					let arr = []
					filters.forEach((el, j) => {
						let obj = {
							c_opt: el.optindex || '',
							c_op: el.index || '',
							c_detail: el.expandval || ''
						}
						arr.push(obj)
					})
					//console.log(filters)
					item.active = item.answer
					item.answer = arr
				})
				return temp
			},
			// 校验
			verify(params) {
				//console.log(params)
				let current = -1
				let msg = null
				aaa:
					for(let index = 0; index < params.length; index++) {
						let item = params[index]
						if(item.requiredFields) { // 必答题
							if(item.subjectType === 0) { // 单选
								if(item.answer.length) { // 已选择
									let match = item.options.filter(opt => opt.isexpand && !opt.expandval && item.active === opt.index)
									if(match.length) { //扩展输入未作答
										current = index
										msg = '请先完成问卷！'
										break
									}
								} else { // 未选择
									current = index
									msg = '请先完成问卷！'
									break
								}
							} else if(item.subjectType === 1) { // 多选
								if(item.answer.length) { // 已选择
									let match = item.options.filter(opt => opt.isexpand && !opt.expandval && item.active.indexOf(opt.index) > -1)
									if(match.length) { // 扩展输入未作答
										current = index
										msg = '请先完成问卷！'
										break
									}
								} else { // 未选择
									current = index
									msg = '请先完成问卷！'
									break
								}
							} else if(item.subjectType === 2) { // 矩阵
								if(item.answer.length) { // 已选择
									let options = item.options
									bbb:
										for(var i = 0; i < options.length; i++) {
											let opts = options[i]
											let match = opts.options.filter(opt => opt.isexpand && !opt.expandval && item.active[i] === opt.index)
											if(!item.active[i] || match.length) { // 行标题未选择或扩展输入未作答 
												current = index
												msg = '请先完成问卷！'
												break aaa
											}
										}
								} else { // 未选择
									current = index
									msg = '请先完成问卷！'
									break
								}
							} else if(item.subjectType === 3) { // 简答
								if(!item.active) {
									current = index
									msg = '请先完成问卷！'
									break
								}
							}
						} else {
							if(item.subjectType === 0) { // 单选
								let match = item.options.filter(opt => opt.isexpand && !opt.expandval && item.active === opt.index)
								if(match.length) { //扩展输入未作答
									current = index
									msg = '请先完成问卷！'
									break
								}
							} else if(item.subjectType === 1) { // 多选
								let match = item.options.filter(opt => opt.isexpand && !opt.expandval && item.active.indexOf(opt.index) > -1)
								if(match.length) {
									current = index
									msg = '请先完成问卷！'
									break
								}
							} else if(item.subjectType === 2) { // 矩阵
								let options = item.options
								bbb:
									for(var i = 0; i < options.length; i++) {
										let opts = options[i]
										let match = opts.options.filter(opt => opt.isexpand && !opt.expandval && item.active[i] === opt.index)
										if(match.length) { // 扩展输入未作答 
											current = index
											msg = '请先完成问卷！'
											break aaa
										}
									}
							}
						}
					}

				if(current !== -1) {
					let top = document.getElementById('question' + current).offsetTop
					window.scrollTo(0, top)
				}
				return msg
			},
			//确定提交
			async sureSubmit(params) {
				let para = {
					studentId: this.info.id,
					estimateId: this.$route.query.queid,
					jsonData: JSON.stringify(params)
				}
				let res = await this.$Request.post('/estimate/saveQiByWeChat.do', para)
				if(res && res.code === 0) {
					this.gobackTimer = setTimeout(() => {
						if(history.length > 2) {
							history.back()
						} else {
							this.$router.replace({
								path: '/questionlist',
								query: {
									id: this.$route.query.id
								}
							})
						}
					}, 1500)
					this.$toast.success('提交成功！')
				}
			}
		},
		beforeDestroy() {
			clearTimeout(this.gobackTimer)
		}
	}
</script>

<style>

</style>


